import React, {Component} from 'react';
import Slider from 'react-slick';
import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import * as blocksActions from '../../actions/blocksActions';
import * as toolsActions from '../../actions/toolsActions';

class LeftNavButton extends Component {
    render() {
        return <div onClick={this.props.onClick} className="prevArrow"><div/></div>
    }
}
class RightNavButton extends Component {
    render() {
        return <div onClick={this.props.onClick} className="nextArrow"><div src="/img/nextArrow.png"/></div>
    }
}

class Blocks extends Component{
    constructor(props){
        super(props);

    }

    componentWillMount(){
        const { initBlocks } = this.props.blocksActions;
        let arrayBlocks = {};
        let {...mapping} = this.props.map.mapping;
        for(let key in mapping){
            arrayBlocks[key] = mapping[key];
            console.log(key.type);
        }
        console.log('INIT_BLOCKS',arrayBlocks);
        initBlocks(arrayBlocks);
    }

    selectBlockHandle(num){

        this.props.blocksActions.setSelectedBlock(num);

        let newArray = this.props.map.source;

        this.props.map.selectedItem.map( (item)=>{
            newArray[item.x][item.y] = num;
        });
    }

    wheelHandle(e = window.event){
        this.setState({eventType: e.type});
        if(e.deltaY > 0)
            this.slider.slickNext();
        else
            this.slider.slickPrev();
    }

    render(){


        const settings = {
            draggable: false,
            initialSlide: 0,
            infinite: false,
            speed: 500,
            slidesToShow: 8,
            slidesToScroll: 3,
            className: 'blocksCarousel',
            prevArrow: <LeftNavButton/>,
            nextArrow: <RightNavButton/>,
            variableWidth: true,
        };
        let sliderContent = [];
        this.props.blocks.blocks.map( (item,i)=> {
            let temp = this.props.map.mapping[i];
            if (this.props.blocks.selectedBlock === i)
                temp += ' selected';

            temp += ' block';

            sliderContent.push(
                <div
                    className={temp}
                    key={i}
                    onClick={this.selectBlockHandle.bind(this,i)}
                />);
        });

        if(!sliderContent.length){
            sliderContent = (<div className="block">Loading...</div>);
        }

        return(
            <div className="blocksContainer">
                <div className="blocks"
                     onWheel={this.wheelHandle.bind(this,event)}
                >
                    <Slider ref={c => this.slider = c } {...settings}>
                        {sliderContent}
                    </Slider>
                </div>
            </div>
        );
    }
}

function mapStateToProps (state) {
    return {
        blocks: state.blocksReducer,
        map: state.mapReducer,
    }
}

function mapDispatchToProps(dispatch) {
    return{
        blocksActions: bindActionCreators(blocksActions, dispatch),
        toolsActions: bindActionCreators(toolsActions, dispatch),
    };
}


export default connect(mapStateToProps, mapDispatchToProps)(Blocks);